@import url(https://fonts.googleapis.com/css2?family=Actor&display=swap);
body {
  margin: 0;
  font-family: "Actor", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  background-color: whitesmoke;
  font-family: "Montserrat", sans-serif;
}

.menBar {
  display: inline;
}

img {
  float: left;
  height: 100px;
}

.menuButtons {
  justify-content: flex-end;
  display: flex;
  list-style-type: none;
  cursor: pointer;
  padding: 30px;
  margin: 0px;
}

.menuButtons :hover {
  background-color: white;
  color: black;
}

header li {
  padding: 10px;
  background-color: darkblue;
  border-radius: 10px;
  margin: 15px;
}

a {
  text-decoration: none;
  color: white;
  background-color: darkblue;
}

.institucionales {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

.servicios {
  width: 70%;
  margin-left: 200px;
}

.handSpray {
  padding-right: 20px;
  float: right;
  margin-right: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 300px;
  height: auto;
}

.nosotros {
  width: 70%;
  margin: 0 auto;
}

.nosotros p {
  margin: 20px 0;
}

img.chemImage {
  padding-right: 20px;
  float: right;
  margin-right: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 350px;
  height: auto;
}

img.probe {
  padding-right: 20px;
  float: left;
  margin-right: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 350px;
  height: auto;
}

.productosUl ul {
  list-style-type: none;

  margin-left: 250px;
}

.productosUl li {
  padding: 10px;
  background-color: darkblue;
  border-radius: 10px;
  margin: 15px;
  color: white;
  text-align: center;
  width: 500px;
}

.contacto {
  text-align: center;
}

footer li {
  padding: 10px;
  background-color: darkblue;
  border-radius: 10px;
  margin: 15px;
}

.steelTank {
  height: 400px;
  padding-right: 20px;
}

.limpiadores ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

.limpiadores {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

.bottle,
.hands {
  padding-right: 20px;
  float: right;
  margin-right: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 350px;
  height: auto;
}

.milk {
  float: right;
  width: 450px;
  height: auto;
  margin-top: 30px;
}

.orange,
.food {
  float: left;
  width: 450px;
  height: auto;
  margin-right: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
}

